	module.exports = {"data":{"markdownRemark":{"html":"<p>There are certain characters in HTML that are reserved by the language, for example <code>&#x3C;</code> and <code>></code>. Using them in an HTML file for something other than tags may result in errors because your browser may interpret them as tags. One way to get around this issue is using HTML Entities:</p>\n<p><code>&#x3C;</code> can be written as <code>&#x26;lt;</code> while <code>></code> can be written as <code>&#x26;gt;</code> and they will render as expected in the browser.</p>\n<p>A full listing of HTML entities for other reserved characters and characters not in your keyboard such as © and ∑ can be found at <a href='https://dev.w3.org/html5/html-author/charref' target='_blank' rel='nofollow'>https://dev.w3.org/html5/html-author/charref</a>.</p>","fields":{"slug":"/miscellaneous/learn-about-html-entities/"},"frontmatter":{"title":"Learn About HTML Entities"}}},"pathContext":{"slug":"/miscellaneous/learn-about-html-entities/"}}

